@import '../../variables.scss';

.details{
    width: 30vw;
    pointer-events: all;
    .train-headline{
        display: flex;
        gap: .15em;
        .train-icon{
            width: 4em;
            height: 4em;
            margin-left: -10px;
            flex-shrink: 0;
        }
        h2 {
            margin-top: .5em;
        }
    }
    h5{
        margin-bottom: .75em;
        margin-top: 1em;
    }
    position: relative;
    padding: 0 $padding-l $padding-l;
    .devider{
        margin-left: 2px;
        color: #999;
    }
    
    .icon {
        width: 22px;
        height: 22px;
        padding: 3px;
        background: $background-dark;
        border-radius: 3px;
        margin-right: 5px;
        img {
            vertical-align: top;
        }
    }
    .destinationslist {
        li {
            margin-top: 3px;
            position: relative;
            background: white;
            box-shadow: 3px 3px 8px rgba(0,0,0,0.1); 
            padding: .5em;
            margin-bottom: .35em;
            transition: all .35s ease;
            cursor: pointer;
            display: flex;
            align-items: center;
            gap: .5em;
            .velo-icon{
                width: 1.75em;
                height: 1.75em;
                rect.bg{
                    fill: #ccc;
                    transition: all .35s ease;
                }
            }
            h4.veloroute {
                font-family: 'Source Sans Pro', sans-serif;
                font-weight: 300;
            }
            &:before {
                content: '';
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                border-top: .5em solid transparent;
                border-bottom: 10px solid transparent;
                border-left: 0em solid white;
                transition: all .35s ease;
            }
            &.active,
            &:hover {
                transition: all .35s ease;
                background: white;
                box-shadow: 5px 3px 10px rgba(0,0,0,0.15); 
                color: $veloroute-hover;

                .velo-icon{
                    rect.bg{
                        fill: $veloroute;
                        transition: all .35s ease;
                    }
                }
            }
            &.active {
                &:before {
                    width: 0; 
                    height: 0; 
                    right: -.5em;
                    border-top: .5em solid transparent;
                    border-bottom: 10px solid transparent;
                    border-left: .5em solid white;
                    transition: all .35s ease;
                }
                
            }
        }
        
        .stations {
            display: flex;
            font-size: .85em;
        }
    }
}

#destination{
    h2 {
        color: $primary-hover;
    }
}